<template>
	<view class="content">
		<u-toast ref="uToast" />

		<u-cell-group class="group">
			<view class="warp">
				<view class="warp-mask">
					<u-row class="person">
						<u-col span="2">
							<view class="avatarThe">
								<image  mode="aspectFill" :src="user.avatar"></image>
							</view>
						</u-col>
						<u-col span="4" style="font-size:19px; font-weight: 600;">
							<view style="display: flex; justify-content: space-around;">
								{{user.username}}
								<u-icon name="scan"></u-icon>
							</view>
							<view>
								<u-line-progress active-color="#2a63ae" :percent="40"></u-line-progress>
							</view>
						</u-col>
						<u-col span="5">
							<view style="font-size: 15px;text-align: right;" @click="setAvatar">
								编辑
							</view>
						</u-col>
						<u-col span="1">
							<view style="font-size: 16px;">
								<u-icon name="arrow-right"></u-icon>
							</view>
						</u-col>
					</u-row>
				</view>
			</view>
			<u-cell-item icon="setting" title="个人设置" @click="set"></u-cell-item>
			<view style="	background-color: #dadfdf;height: 10px;">
			</view>
			<u-cell-item icon="eye" title="附近"></u-cell-item>
			<u-cell-item icon="star" title="兴趣"></u-cell-item>
			<u-cell-item icon="gift" title="超市特卖"></u-cell-item>
			<view style="	background-color: #dadfdf;height: 10px;">
			</view>
			<u-cell-item icon="chat" title="留言记录" @click="message"></u-cell-item>
			<u-cell-item icon="order" title="工作" @click="work"></u-cell-item>
		</u-cell-group>
		<view style="margin-left: 50px;margin-right: 50px;margin-top: 20px;">
			<u-button type="primary" @click="toLogin">{{login}}</u-button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				//本人
				user: {
					username: '游客',
					avatar: '/static/avatar.png',
				},
				reUser:{
					username: '游客',
					avatar: '/static/avatar.png',
				},
				userId: false,
				admin: '',

				//登录
				login: '去登录/退出登录',
			}
		},
		onLoad() {
			// this.userId = this.getStorageId();
			// this.admin = this.getStorageAdmin();
			
			// if(!this.userId)
			// {
			// 	this.user = this.reUser
			// }
			// else
			// this.getUser();
			
		},
		onShow() {
			
			this.userId = this.getStorageId();
			this.admin = this.getStorageAdmin();
			
			if(!this.userId)
			{
				this.user = this.reUser
			}
			else
			this.getUser();
			if (this.admin === 'Y' || this.admin === 'N') {
				this.login = '退出登录'
			} else {
				this.login = '去登录'
			}
		},
	
		methods: {
			getStorageId() {
				const id = uni.getStorageSync('userId')
				//console.log("id"+id)
				return id;
	
			},
			getStorageAdmin() {
				const admin = uni.getStorageSync('admin')
				//console.log(admin)
				return admin;
			},
			//获取本人头像和名字
			getUser() {
				uni.request({
					url: this.apiServer + '/users/userInfo?userId=' + this.userId,
					method: 'GET',
					header: {
						'content-type': 'application/json'
					},
					success: res => {
						this.user = res.data.data.user
					},
					
				});

			},
			setAvatar() {
				if (this.admin === 'Y' || this.admin === 'N') {
					uni.navigateTo({
						url: '/pages/tabbar/tabbar-4/avatar'
					});
				} else {
					console.log('你没登录')
					this.$refs.uToast.show({
						position: 'top',
						title: '请先登录',
						type: 'error',
					})
				}

			},
			set() {
				if (this.admin === 'Y' || this.admin === 'N') {
					uni.navigateTo({
						url: '/pages/tabbar/tabbar-4/set'
					});
				} else {
					console.log('你没登录')
					this.$refs.uToast.show({
						position: 'top',
						title: '请先登录',
						type: 'error',
					})
				}
			},
			work() {
				if (this.admin === 'Y') {
					uni.navigateTo({
						url: '/pages/tabbar/tabbar-4/table'
					});
				} else {
					console.log('你没登录/你不是管理员')
					this.$refs.uToast.show({
						position: 'top',
						title: '请先登录或者申请成为管理员',
						type: 'error',
					})
				}
			},
			message() {
				if (this.admin === 'Y') {
					uni.navigateTo({
						url: '/pages/tabbar/tabbar-4/message'
					});
				} else {
					console.log('你没登录/你不是管理员')
					this.$refs.uToast.show({
						position: 'top',
						title: '请先登录或者申请成为管理员',
						type: 'error',
					})
				}
			},
			toLogin() {
				if (this.admin === 'Y' || this.admin === 'N') {
					uni.setStorageSync('username',false)
					uni.setStorageSync('userId', false)
					uni.setStorageSync('admin',false)
					this.login = '退出登录'
				} else {
					this.login = '去登录'
				}
				uni.navigateTo({
					url: '/pages/tabbar/tabbar-1/new'
				})
				// 将返回的数据注入内存,同步接口
				// uni.setStorageSync('userId', null)
				// uni.setStorageSync('admin', null)
			}
		}
	}
</script>

<style lang="scss">
	.content {
		text-align: center;
		margin-top: -0.5upx;
		height: 573px;
		background-color: #dddddd;
	}


	.warp {
		color: #FFFFFF;
		height: 300upx;
		background-image: url(data:image/jpeg;base64,);
		background-repeat: no-repeat;
		background-size: 100% 134%;
		-moz-background-size: 100% 134%;

	}

	.warp-mask {
		height: 100%;
		width: 100%;
		background: rgba(0, 0, 0, 0.6);
	}

	.person {
		width: 100%;
		padding-top: 10px;
		padding-left: 5px;

	}
	
	.avatarThe {
		image {
			width: 88rpx;
			height: 88rpx;
			border-radius: 50%;
			background-color: #f2f2f2;
		}
	}
</style>
